#btn-start,
#btn-select {
	width: 40px;
	height: 15px;
	border: 2px solid rgba(170, 176, 177, 0.5);
	background-color: rgb(83, 60, 90);
	margin-left: 15px;
	margin-right: 15px;
	border-radius: 25%;
	pointer-events: all;
}

